/* ================= —Å—Ç–∏–ª—ñ–∑—É—î –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è –≤—Å—ñ—Ö –¥–µ–≤–∞–π—Å—ñ–≤ ===============*/
%container {
  padding: 0 20px;
  margin: 0 auto;

  @media screen and (min-width: map-get($breakpoints, 'phone')) {
    width: 480px;
  }

  @media screen and (min-width: map-get($breakpoints, 'tablet')) {
    padding: 0 32px;
    width: 768px;
  }

  @media screen and (min-width: map-get($breakpoints, 'desktop')) {
    padding: 0 16px;
    width: 1200px;
  }
}

/* =================== visually-hidden =================*/
%visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* =================== —Ü–µ–Ω—Ç—Ä—É—î –µ–ª–µ–º–µ–Ω—Ç =================*/
%centred {
  margin-left: auto;
  margin-right: auto;
}

/* =================== —Å—Ç–∏–ª—ñ–∑—É—î –ª–µ–π–±–ª —Å–µ–∫—Ü—ñ—ó –¥–ª—è –≤—Å—ñ—Ö –¥–µ–≤–∞–π—Å—ñ–≤ =================*/
%lable {
  font-weight: 800;
  font-size: 18px;
  line-height: 1.2;
  text-align: center;
  text-transform: uppercase;

  @media screen and (min-width: map-get($breakpoints, 'tablet')) {
    font-size: 22px;
  }

  @media screen and (min-width: map-get($breakpoints, 'desktop')) {
    font-size: 30px;
    letter-spacing: 0.04em;
  }
}

/* =================== —Å—Ç–∏–ª—ñ–∑—É—î –∑–∞–≥–æ–ª–æ–≤–æ–∫ —Å–µ–∫—Ü—ñ—ó –¥–ª—è –≤—Å—ñ—Ö –¥–µ–≤–∞–π—Å—ñ–≤ =================*/
%title {
  font-family: var(--heading-font);
  font-weight: 400;
  font-size: 30px;
  line-height: 1.25;
  letter-spacing: 0.04em;
  text-align: center;
  text-transform: uppercase;

  @media screen and (min-width: map-get($breakpoints, 'tablet')) {
    font-size: 34px;
  }

  @media screen and (min-width: map-get($breakpoints, 'desktop')) {
    font-size: 36px;
    letter-spacing: 0.06em;
  }
}

/* =================== —Å—Ç–∏–ª—ñ–∑—É—î –∫–Ω–æ–ø–∫—É submit —É –º–æ–¥–∞–ª—Ü—ñ –¥–ª—è –≤—Å—ñ—Ö –¥–µ–≤–∞–π—Å—ñ–≤ =================*/
%submit-button {
  width: 114px;
  height: 42px;
  padding: 12px 34px;
  border-radius: 10px;

  display: flex;
  align-items: center;
  text-align: center;

  background-color: var(--button-yellow);

  color: var(--text-black);
  font-weight: 600;
  font-size: 12px;
  line-height: 1.3;

  @media screen and (min-width: map-get($breakpoints, 'tablet')) {
    width: 121px;
    font-size: 14px;
  }
}
