/* ====================== —Ä–æ–∑—Ä–∞—Ö–æ–≤—É—î –≤–∏—Å–æ—Ç—É —Ç–µ–∫—Å—Ç–æ–≤–æ—ó —Å—Ç—Ä–æ–∫–∏ =======================*/
@mixin font-size($size, $height) {
  font-size: $size;
  line-height: calc($height / $size);
}

/* ============================ –¥–æ–¥–∞—î —Å–∫—Ä—É–≥–ª–µ–Ω—ñ –ø—Å–µ–≤–¥–æ–µ–ª–µ–º–µ–Ω—Ç–∏ —É —Å–µ–∫—Ü—ñ—è—Ö  =========================*/
@mixin pseudo($width, $height) {
  display: block;
  content: '';
  width: $width;
  height: $height;
  border-radius: 50%;
}

/* ============================ –¥–ª—è —Ä–æ–±–æ—Ç–∏ –∑ —Ñ–ª–µ–∫—Å–∞–º–∏ ===========================*/
/* ------- —Å—Ç–∞–≤–∏—Ç—å gap, –≤–∏—Ä—ñ–≤–Ω—é—î –æ–±'—î–∫—Ç–∏ –ø–æ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—ñ —ñ –ø–æ –≤–µ—Ä—Ç–∏–∫–∞–ª—ñ (–∑–∞ –∑–∞–º–æ–≤—á–µ–Ω–Ω—è–º –ø–æ —Ü–µ–Ω—Ç—Ä—É) ------- */
// –∑–Ω–∞—á–µ–Ω–Ω—è null –Ω–∞ –º—ñ—Å—Ü—ñ –∑–º—ñ–Ω–Ω–æ—ó –ø—Ä–∏–±–∏—Ä–∞—î –ø–∞—Ä–∞–º–µ—Ç—Ä (—Å—Ç—Ä–æ–∫—É –∫–æ–¥—É) –∑ –º—ñ–∫—Å—ñ–Ω—É
@mixin flex($gap, $x: center, $y: center) {
  display: flex;
  gap: $gap;
  justify-content: $x;
  align-items: $y;
}

/* ------- —Ä–æ–∑—Ä–∞—Ö–æ–≤—É—î –ø–∞—Ä–∞–º–µ—Ç—Ä flex-basis ------- */
// ($n - –∫—ñ–ª—å–∫—ñ—Å—Ç—å –µ–ª–µ–º–µ–Ω—Ç—ñ–≤ —É —Ä—è–¥–∫—É; $gap - –∑–Ω–∞—á–µ–Ω–Ω—è gap)
@mixin flex-basis($n, $gap) {
  flex-basis: calc((100% - ($gap * ($n - 1))) / $n);
}

/* ============================= –ø–æ–∑–∏—Ü—ñ–æ–Ω—É—î –µ–ª–µ–º–µ–Ω—Ç–∏ =======================*/
// ($position - –≤–∏–¥ –ø–æ–∑–∏—Ü—ñ–æ–Ω—É–≤–∞–Ω–Ω—è (relative/absolute/fixed)
// $top-or-bottom - —Å—Ç–∞–≤–∏—Ç—å—Å—è –∑–Ω–∞—á–µ–Ω–Ω—è top –∞–±–æ bottom
// $y - –∑–º—ñ—â–µ–Ω–Ω—è –≤—ñ–¥ –≤–µ—Ä—Ö—É/–Ω–∏–∑—É –≤ px
// $left-or-right - —Å—Ç–∞–≤–∏—Ç—å—Å—è –∑–Ω–∞—á–µ–Ω–Ω—è left –∞–±–æ right
// $x - –∑–º—ñ—â–µ–Ω–Ω—è —Å–ø—Ä–∞–≤–∞/–∑–ª—ñ–≤–∞ –≤ px
@mixin position(
  $position: absolute,
  $top-or-bottom,
  $y: 0,
  $left-or-right,
  $x: 0
) {
  position: $position;
  #{$top-or-bottom}: $y;
  #{$left-or-right}: $x;
}

/* ============================ —Å—Ç–∞–≤–∏—Ç—å bg-picture –¥–ª—è —Ä—ñ–∑–Ω–∏—Ö —Ä–µ—Ç–∏–Ω–∞-–¥–∏—Å–ø–ª–µ—ó–≤ ======================*/
/* ------- –¥–ª—è –∫–∞—Ä—Ç–∏–Ω–æ–∫ –Ω–∞ –æ–¥–∏–Ω –¥–µ–≤–∞–π—Å ------- */
// $folder - –Ω–∞–∑–≤–∞ –ø–∞–ø–∫–∏, –¥–µ –∑–±–µ—Ä—ñ–≥–∞—î—Ç—å—Å—è –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è. –Ø–∫—â–æ —à–ª—è—Ö –¥–æ —Ñ–∞–π–ª—É —Å–∫–ª–∞–¥–∞—î –±—ñ–ª—å—à–µ –Ω—ñ–∂ 1 –ø–∞–ø–∫—É (–Ω–∞–ø—Ä–∏–∫–ª–∞–¥ images/backgroung/mobile),
// —É –ø–∞—Ä–∞–º–µ—Ç—Ä—ñ –≤–∫–∞–∑—É—é—Ç—å—Å—è —á–µ—Ä–µ–∑ / –≤—Å—ñ –ø–∞–ø–∫–∏  –∫—Ä—ñ–º images (–Ω–∞–ø—Ä–∏–∫–ª–∞–¥ backgroung/mobile)
// $picture-name - –Ω–∞–∑–≤–∞ –∫–∞—Ä—Ç–∏–Ω–∫–∏ –±–µ–∑ -min —Ç–∞ —Ñ–æ—Ä–º–∞—Ç—É (–Ω–∞–ø—Ä–∏–∫–ª–∞–¥ —è–∫—â–æ –∫–∞—Ä—Ç–∏–Ω–∫–∞ –Ω–∞–∑–∏–≤–∞—î—Ç—å—Å—è "cow-min.png", –≤–∫–∞–∑—É—î—Ç–µ "cow")
// $format - —Ñ–æ—Ä–º–∞—Ç –∫–∞—Ä—Ç–∏–Ω–∫–∏ (jpg –∞–±–æ png)
@mixin bg-picture($folder, $picture-name, $format) {
  background-image: url('../images/#{$folder}/#{$picture-name}-min.#{$format}');

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url('../images/#{$folder}/#{$picture-name}@2x-min.#{$format}');
  }

  @media (min-device-pixel-ratio: 3),
    (min-resolution: 288dpi),
    (min-resolution: 3dppx) {
    background-image: url('../images/#{$folder}/#{$picture-name}@3x-min.#{$format}');
  }
}

/* ============================= a–Ω—ñ–º—É—î –≤–ª–∞—Å—Ç–∏–≤–æ—Å—Ç—ñ =======================*/
/* ------- –æ–¥–Ω—É –≤–ª–∞—Å—Ç–∏–≤—ñ—Å—Ç—å ------- */
@mixin animation($property) {
  transition: $property $animation;
}

/* ------- –¥–≤—ñ –≤–ª–∞—Å—Ç–∏–≤–æ—Å—Ç—ñ ------- */
@mixin animation-m($property-1, $property-2) {
  transition: $property-1 $animation, $property-2 $animation;
}

/* ------- —Ç—Ä–∏ –≤–ª–∞—Å—Ç–∏–≤–æ—Å—Ç—ñ ------- */
@mixin animation-l($property-1, $property-2, $property-3) {
  transition: $property-1 $animation, $property-2 $animation,
    $property-3 $animation;
}

/* ============================ —Å—Ç–≤–æ—Ä—é—î –º–µ–¥—ñ–∞–ø—Ä–∞–≤–∏–ª–∞ ======================*/
// $max-or-min - —Å—Ç–∞–≤–∏—Ç—å—Å—è –∑–Ω–∞—á–µ–Ω–Ω—è max –∞–±–æ min
// $device - –≤–∫–∞–∑—É—î—Ç—å—Å—è –Ω–∞–∑–≤–∞ —Ç–∏–ø—É –¥–µ–≤–∞–π—Å–∞ (phone/tablet/desktop)
@mixin media($max-or-min: min, $device) {
  @if $max-or-min == max {
    @media screen and (#{$max-or-min}-width: (map-get($breakpoints, '#{$device}') - 0.5px)) {
      @content;
    }
  } @else {
    @media screen and (#{$max-or-min}-width: map-get($breakpoints, '#{$device}')) {
      @content;
    }
  }
}

/* =================== —Å—Ç–∏–ª—ñ–∑—É—î –∫–Ω–æ–ø–∫–∏ —É —Ä—ñ–∑–Ω–∏—Ö —Å—Ç–∞–Ω–∞—Ö =================*/
// $type - –æ–±–æ–≤'—è–∑–∫–æ–≤–µ –∑–Ω–∞—á–µ–Ω–Ω—è, –æ–±–∏—Ä–∞—î —Ç–∏–ø —Å—Ç–∏–ª—ñ–∑–∞—Ü—ñ—ó. –ó–Ω–∞—á–µ–Ω–Ω—è (color, stroke, text) –æ–±–∏—Ä–∞—î—Ç—å—Å—è –∑ –Ω–∞—Å—Ç—É–ø–Ω–∏—Ö:
// –Ø–∫—â–æ –∫–Ω–æ–ø–∫–∞ –º–∞—î —Ñ–æ–Ω–æ–≤–∏–π –∫–æ–ª—ñ—Ä —ñ –ø—Å–µ–≤–¥–æ–µ–ª–µ–º–µ–Ω—Ç - color;
// –Ø–∫—â–æ –∫–Ω–æ–ø–∫–∞ –º–∞—î –∫–æ–ª—å–æ—Ä–æ–≤—É —Ä–∞–º–∫—É —ñ –ø—Å–µ–≤–¥–æ–µ–ª–µ–º–µ–Ω—Ç - stroke;
// –Ø–∫—â–æ —Ç–µ–∫—Å—Ç –º—ñ—Å—Ç–∏—Ç—å —Ç—ñ–ª—å–∫–∏ –ø–æ—Å–∏–ª–∞–Ω–Ω—è –≤—ñ–¥–ø–æ–≤—ñ–¥–Ω–æ–≥–æ –∫–æ–ª—å–æ—Ä—É - text
// $size - —Ä–æ–∑–º—ñ—Ä —à—Ä–∏—Ñ—Ç—É –≤ –ø—ñ–∫—Å–µ–ª—è—Ö (–ø–æ –º–∞–∫–µ—Ç—É)
// $height - —Ä–æ–∑–º—ñ—Ä –≤–∏—Å–æ—Ç–∏ —Å—Ç—Ä–æ–∫–∏ –≤ –ø—ñ–∫—Å–µ–ª—è—Ö (–ø–æ –º–∞–∫–µ—Ç—É)
@mixin button-styles($type, $size: 14px, $height: 18px) {
  padding: 12px 34px;
  border-radius: 10px;

  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;

  color: var(--text-black);
  font-weight: 600;
  font-size: $size;
  line-height: calc($height / $size);

  transition: background-color $animation, background-image $animation;

  &::after {
    display: block;
    content: '';
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-image: url(../images/button-bracket-black.svg);
    background-size: contain;
    background-repeat: no-repeat;
    margin-left: 6px;
  }

  &:focus {
    color: var(--text-white);
    outline: none;

    &::after {
      background-image: url(../images/button-bracket-white.svg);
    }
  }

  @if $type == color {
    background-color: var(--button-yellow);
    transition: background-color $animation, filter $animation;

    &:hover,
    &:focus {
      background-color: var(--button-green);
      filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    }
    &:disabled {
      background-color: var(--button-grey);
    }
  } @else if $type == stroke {
    background-color: transparent;
    border: 2px solid var(--button-yellow);
    transition: border $animation, filter $animation;

    &:hover,
    &:focus {
      border: 2px solid var(--button-green);
      filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    }
    &:disabled {
      border: 2px solid var(--button-grey);
    }
  } @else if $type == text {
    background-color: transparent;
    border: none;
    color: var(--button-yellow);
    transition: color $animation, filter $animation;
    &::after {
      display: none;
    }

    &:hover,
    &:focus {
      color: var(--button-green);
      filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    }
    &:disabled {
      color: var(--button-grey);
    }
  }
}
